<script>
import { mapState } from 'vuex';
import HtmlWithLinks from '@/components/HtmlWithLinks.vue';
import VAccordion from '@/components/Accordion.vue';
import SideloadingOptions from '@/components/SideloadingOptions.vue';
import Btn from '@/components/Button.vue';

export default {
  components: {
    HtmlWithLinks,
    VAccordion,
    SideloadingOptions,
    Btn,
  },
  data() {
    return {
      officialOptions: [],
      thirdPartyOptions: [],
      plusOptions: [],
      devCredentialsProviders: [],
    };
  },
  computed: {
    title() {
      return this.$t('HOW_TO_SIDELOAD_AND_USE_PLUS');
    },
    ...mapState(['theme']),
  },
  methods: {
    purchase(e) {
      if (!e.target?.matches('a[href="#"]')) {
        return;
      }
      e.preventDefault();

      this.$alert(`<div class="g-text">${this.$t('PLUS_DOWNLOAD_APP', { href: 'https://apps.apple.com/us/app/apple-developer/id640199958' })}</div>`);
    },
    importDevCredetials(href) {
      this.$confirm(this.$t('IMPORT_DEV_CREDENTIALS_PROVIDER_CONFIRMATION'))
        .then(() => {
          window.location.href = href;
        });
    },
  },
  created() {
    this.$api.getSideloadingOptions('official').then((response) => {
      this.officialOptions = response;
    });

    this.$api.getSideloadingOptions('3rdparty').then((response) => {
      this.thirdPartyOptions = response;
    });

    /* this.$api.getDevCredentialsProviders()
      .then((response) => {
        this.devCredentialsProviders = response;
      }); */

    this.$api.getSideloadingOptions('plus').then((response) => {
      this.plusOptions = response;
    });
  },
};
</script>

